import { Cascader, CascaderPanel,Icon ,Form,FormItem,Input,Button,Calendar } from "element-ui";
const element = {
  install: function(Vue) {
    Vue.use(Cascader);
    Vue.use(Icon)
    Vue.use(CascaderPanel);
    Vue.use(Form)
    Vue.use(FormItem)
    Vue.use(Input)
    Vue.use(Button)
    Vue.use(Calendar)
  }
};
export default element;



