import Vue from 'vue'
import Router from 'vue-router'
import bscaidan from '@/views/bscaidan'
import index from "@/views/index"
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/bscaidan',
      name: 'bscaidan',
      component: bscaidan
    }
    
  ]
})



