import Vue from "vue"
import Vuex from "vuex"
//引入cart的vuex模块
// import cart from "@/store/modules/cart.js"
Vue.use(Vuex)
export default new Vuex.Store({
	//当做data
	state:{
		levelid:Number,//角色id
        accid:Number, //企业id
		accesskey:'', //秘钥匙
		userid:Number,//用户id
		timestamp:'',//unix 时间戳
		signature:''//签名
	},
	//相当于计算属性
	getters:{
	},
	//同步一些方法
	mutations:{
	},
	//存放异步的方法
	actions:{
	}
})



